.site-layout-background {
  display: flex;
  width: 100%;
  justify-content: space-between;
  position: sticky;
  top: 0;
  z-index: 100;
  align-items: center;
  height: 100px;
}

.header-right {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
}

.custom-menu {
  display: flex;
  justify-content: flex-end;
}


.header-logo {
  height: 100%; /* Prevent scaling of the logo */
}

/* Logo */
.site-layout-background a {
  cursor: pointer;
}

/* Logo image */
.site-layout-background img {
  max-width: 64px;
  height: auto;
}

/* Header text */
.site-layout-background .ant-typography {
  margin-left: 5rem;
}

/* Custom menu items */
.custom-menu-item {
  flex: 1;
  text-align: center;
}

/* Add hover styles to the custom menu items */
.custom-menu-item:hover {
  background-color: #1890ff; /* Highlight color on hover */
}

/* Adjust the text color on hover if needed */
.custom-menu-item:hover a {
  color: #fff; /* Text color on hover */
}