/* App.css */

/* Header */
.header {
    background: #001529;
  }
  
  /* Logo - You can customize this as needed */
  .logo {
    height: 32px;
    width: 120px;
    background: rgba(255, 255, 255, 0.2);
    margin: 16px 28px 16px 0;
    float: left;
  }
  .disable-select {
    user-select: none;
  }
  
  /* Expand Button */
  .expand-button {
    padding: 16px;
    cursor: pointer;
    color: #fff; /* White text color */
  }
  
  /* Menu */
  .ant-menu {
    background: #001529; /* Background color of the Menu */
    color: #fff; /* Text color of the Menu */
  }
  
  /* Menu Item */
  .ant-menu-item, .ant-menu-submenu-title {
    color: #fff; /* Text color of Menu Items and Submenu Titles */
  }
  
  /* Active Menu Item */
  .ant-menu-item-selected {
    background: #1890ff; /* Background color of the selected Menu Item */
  }
  